import React, { Component } from 'react';
import { Link, Redirect, withRouter } from 'react-router-dom'
import AuthService from '../../../auth/authService'
import PropTypes from 'prop-types'
import queryString from 'query-string'
import { Button, Card, CardBody, CardGroup, Col, Container, Form, Input, InputGroup, InputGroupAddon, InputGroupText, Row } from 'reactstrap';

class Login extends Component {

  constructor(props) {
    super(props)
    this.Auth = new AuthService('https://cursosconciencia.herokuapp.com')
  }

  state = {
    loggedIn: false,
    loginRedirect: false,
    errorSignin: false,
    signupReferer: false
  }

  static propTypes = {
    match: PropTypes.object.isRequired,
    location: PropTypes.object.isRequired,
    history: PropTypes.object.isRequired
  }

  componentDidMount() {
    const values = queryString.parse(this.props.location.search)
    if (values.referer) {
      this.setState({ signupReferer: true })
    }
  }

  componentWillMount() {
    if (this.Auth.loggedIn()) {
      this.setState({ loggedIn: true })
    }
  }

  handleLogin = (event) => {
    event.preventDefault()

    this.Auth.login(this.state.email, this.state.password)
      .then((res) => {
        this.setState({ loggedIn: true })
      })
      .catch((err) => {
        if (err.response.status === 401) {
          this.setState({ errorSignin: true })
          console.log('eroor')
        }
      })
  }

  HandleEmail = (event) => this.setState({ email: event.target.value })
  HandlePassword = (event) => this.setState({ password: event.target.value })



  render() {

    let loggedIn = this.state.loggedIn



    return loggedIn ? (
      <Redirect to={{ pathname: '/dashboard' }} />
    ) : 

    (
      <div className="app flex-row align-items-center" style={{ backgroundImage: "url('/images/2.jpg')" }}>
        <Container>
          <Row className="justify-content-left">
            <Col md="4">
              <CardGroup>
                <Card className="p-4">
                  <CardBody>
                    <Form>
                      <h2>Ingresa tus datos</h2>
                      <p className="text-muted">Version 2.0</p>
                      <InputGroup className="mb-3">
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <i className="icon-user"></i>
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input type="text" placeholder="Usuario" autoComplete="username" value={this.state.email} onChange={this.HandleEmail}/>
                      </InputGroup>
                      <InputGroup className="mb-4">
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <i className="icon-lock"></i>
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input type="password" placeholder="Contrasea" autoComplete="current-password" value={this.state.password} onChange={this.HandlePassword}/>
                      </InputGroup>
                      <Row>
                        <Col xs="6">
                          <Button color="primary" className="px-4" onClick={this.handleLogin}>Entrar</Button>
                        </Col>
                        <Col xs="6" className="text-right">
                          <Button color="link" className="px-0">Olvid su Contrasea?</Button>
                        </Col>
                      </Row>
                    </Form>
                  </CardBody>
                </Card>
              
              </CardGroup>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

export default withRouter(Login);
